import './index.css'

import {Component} from 'react'

class DestinationItem extends Component {
  render() {
    const {data} = this.props
    return (
      <li className="card">
        <img className="pic" src={data.imgUrl} alt="" />
        <h1 className="heading">{data.name}</h1>
      </li>
    )
  }
}

export default DestinationItem
